.search-bar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 80px;
}

.search-input {
  padding: 18px 40px;
  font-size: 1rem;
  border: none;
  border-radius: 30px 0 0 30px;
  width: 100%;
  max-width: 500px;
  outline: none;
  color: #244855;
  background-color: #FBE9D0;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

.search-button {
  padding: 18px 23px;
  background-color: #E64833;
  border: none;
  border-radius: 0 30px 30px 0;
  color: #FBE9D0;
  font-size: 1rem;
  cursor: pointer;
}

.search-button:hover {
  background-color: #874F41;
}


@media (max-width: 768px) {
  .search-bar-container {
    flex-direction: column;
    gap: 10px;
    padding-top: 40px;
  }

  .search-input {
    border-radius: 30px;
    max-width: 90%;
    width: 90%;
  }

  .search-button {
    border-radius: 30px;
    width: 90%;
  }
}
